* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.nav__list {
  margin-bottom: 30px;
  list-style: none;
}

.nav__link {
  max-width: 150px;
  text-shadow: 1px 1px #fff;
  font-size: 16px;
  font: inherit;
  color: #0f1d4e;
  cursor: pointer;
  background-color: #d9e5fc;
  border-radius: 10px;
  padding: 8px 12px;
  font-weight: bold;
  text-decoration: none;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

.nav__link:hover, .nav__link:focus {
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

button[data-start], button[data-stop] {
  max-width: 150px;
  text-shadow: 1px 1px #fff;
  font-size: 16px;
  font: inherit;
  color: #0f1d4e;
  cursor: pointer;
  background-color: #d9e5fc;
  border: rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-top: 30px;
  margin-right: 30px;
  padding: 8px 12px;
  font-weight: bold;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

button[data-start]:hover, button[data-stop]:hover, button[data-start]:focus, button[data-stop]:focus {
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

button[disabled] {
  color: #61677c;
  background-color: #ebecf0;
}

